	mounted() {
		// 判断用户是否登录
		let userInfo = uni.getStorageSync('userInfo')
		if(userInfo){
			// 更新当前页面userInfo的状态数据
			this.userInfo = JSON.parse(JSON.parse(userInfo).rawData)
		}
	}
